.background{
    width: 100%;
      background: url(./marvel-bg10.png);
      height: 40vh;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    margin-bottom: 80px;
  }
  
  .headline{
    display: flex;
    align-items: center;
    height: 100%;
    
  }
  
  .headline_item{
   color: white;
   font-weight: bold;
   font-size: 50px;
   margin-left: auto;
   margin-right: auto;
   background-color: rgb(212, 21, 21);
   padding: 10px;
  }

  @media (max-width: 767px){
    .headline_item{
      font-size: 2rem;
    }
    .background{
      height: 30vh;
      margin-bottom: 40px;
    }
  }
  